import React, { Component } from 'react';
export var incrementValue = 1;
export var riskLevel = 10;


class Increment extends Component {
    constructor(props) {
      super(props);
      this.state = {
        incrementValue,
        riskLevel,
        show: true
      };

    }
  
    IncrementItem = () => {
      this.setState({ incrementValue: this.state.incrementValue + 0.1 });
      this.setState({ riskLevel: this.state.riskLevel - 1 });
      incrementValue = this.state.incrementValue + 0.1
      riskLevel = this.state.riskLevel - 1


    }
    DecreaseItem = () => {
      this.setState({ incrementValue: this.state.incrementValue - 0.1 });
      this.setState({ riskLevel: this.state.riskLevel + 1 });

      incrementValue = this.state.incrementValue - 0.1
      riskLevel = this.state.riskLevel + 1
    }

  

    render() {
      return (
        <div>
          <button onClick={this.IncrementItem}>Decrease multi risk</button>
          <button onClick={this.DecreaseItem}>Increase multi risk</button>
          <button onClick={this.ToggleClick}>
            { this.state.show ? 'Hide number' : 'Show number' }
          </button>
          { this.state.show ? <h3>Risk level: { riskLevel.toFixed(1) }</h3> : '' }
        </div>
      );
    }
  }
  
  export default Increment
